import {commumal} from "../public";
var audlist={
    methods:{
        init(){
           $(".btnBack").on("click",commumal.back);
           $(".queryMore").on("click",audlist.events.queryMore)
        }
    },
    events:{
        condition(data){
            let html='';
            for(let i=0;i<data.length;i++){
                 html+=`
                    <button data-value="${data[i].OA_TYPE}">${data[i].OA_TYPE_NAME}</button>
                 `
            }
            let $html=$(html);
            $html.eq(0).addClass("select");
            $html.on("click",audlist.events.selectOption)
            $(".condition .buttons").html($html)
        },
        selectOption(){
            let that=$(this);
            that.addClass("select").siblings().removeClass("select");
            audlist.events.fresh()
        },
        fresh(){
            var ky=$(".buttons .select").attr("data-value");
           let postData=audlist.events.freshData(ky);
           // console.log("提交的数据:",postData);
            $.defs.myPost(postData).then((data)=>{
                 let arrangeData=audlist.events.arrange(data.rows,ky)
              //  console.log("arrange:",arrangeData)
                let html=audlist.events.content(arrangeData,ky);
                let $html=$(html);
                 $html.find(".through").on("click",audlist.events.through)
                 $html.on("click",audlist.events.matterItem)
                $(".content").html($html)
            })
        },
        freshData(ky){
            let obj={
                m:"LIST",
                REVIEW_IND:"Y",
            }
            if(ky=="ATDC"){
                obj.SP_NAME="SP_ATDC_ATTENDANCE_INFO_LIST"
            }else if(ky=="RBRB"){
              obj.SP_NAME="SP_RBRB_REIMBURSE_INFO_LIST"
            }else if(ky=="LVLV"){
              obj.SP_NAME="SP_LVLV_LEAVE_INFO_LIST"
            }else if(ky=="OTBL"){
              obj.SP_NAME="SP_OTBL_OVERTIME_BILL_INFO_LIST"
            }
            return obj
        },
        arrange(data,ky){
           if(ky=="ATDC"){
                data.forEach((item)=>{
                     item.ApplyDate=commumal.dateToday(item.ATDC_APPLY_DT);
                })
            }else if(ky=="LVLV"){
                data.forEach((item)=>{
                    item.ApplyDate=commumal.dateToday(item.LVLV_ENTRY_DT);
                    item.toDay=Math.ceil(item.LVLV_HOUR/4)/2;
                })
            }else if(ky=="OTBL"){
                data.forEach((item)=>{
                    item.ApplyDate=commumal.dateToday(item.OTBL_APPLY_DT)
                })
            }
            return data
        },
        content(data,ky){
             let  html='';
            data.forEach((item)=>{
                 if(ky=="ATDC"){
                     html+=`
                     <div class="weui-form-preview item" index="${item.ATDC_KY}" sts="${item.ATDC_STS}" start="${item.ATDC_START_DT}" type="${item.SYSV_ATDC_TYPE}" name="${item.MEME_NAME}">
                      <div class="weui-form-preview__hd">
                        <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black f12">${item.ATDC_ID}<span class="ml15rem">${item.SYSV_ATDC_TYPE_DESC}</span></label>
                          <em class="weui-form-preview__value">${item.ATDC_STS_DESC}</em>
                        </div>
                      </div>
                      <div class="weui-form-preview__bd">
                        <div class="weui-form-preview__item">
                           <label class="weui-form-preview__label black f12">${item.MEME_NAME}</label>
                           <span class="weui-form-preview__value black f12">申请时间:${item.ApplyDate}</span>
                        </div>
                        <div class="weui-form-preview__item">
                             ${audlist.events.businessOrReport(item)}
                        </div>
                        <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black f12">
                              地点:${item.ATDC_SITE}
                          </label>
                        </div>
                      </div>
                       <div class="weui-form-preview__ft">
                            ${audlist.events.reportBtn(item)}
                       </div>
                     </div>
                     `
                 }else if(ky=='RBRB'){
                     html+=`
                     <div class="weui-form-preview item" DATE="${item.RBRB_APPLY_DT}" EHUSER="${item.DPPS_KY}" RBRB_STS="${item.RBRB_STS}" RBRB_KEY="${item.RBRB_KEY_STRING}" fepp="${item.FEPP_NAME}">
                       <div class="weui-form-preview__hd">
                         <label class="weui-form-preview__label black">${item.MEME_NAME}</label>
                         <span class="weui-form-preview__value cost">￥<span class="RBRB_APPLY_AMT">${item.RBRB_APPLY_AMT}</span></span>
                       </div>
                       <div class="weui-form-preview__bd">
                        <div class="weui-form-preview__item">
                           <label class="weui-form-preview__label personName black">发生日期:${item.RBRB_APPLY_DT}</label>
                           <span class="weui-form-preview__value black">${item.RBRB_STS_DESC}</span>
                        </div>
                       </div>
                       <div class="weui-form-preview__ft">
                            ${audlist.events.reimburseBtn(item)}
                       </div>
                     </div>
                     `
                 }else if(ky=="LVLV"){
                     html+=`
                     <div class="weui-form-preview item" index="${item.LVLV_KY}" lvlvId="${item.LVLV_ID}" type="${item.SYSV_LVLV_TYPE_DESC}">
                       <div class="weui-form-preview__hd">
                         <div class="weui-form-preview__item">
                            <label class="weui-form-preview__label black f12">${item.LVLV_ID}</label>
                            <em class="weui-form-preview__value">${item.LVLV_STS_DESC}</em>
                        </div>
                       </div>
                       <div class="weui-form-preview__bd f12">
                        <div class="weui-form-preview__item">
                           <label class="weui-form-preview__label black">${item.MEME_NAME}</label>
                           <span class="weui-form-preview__value black">休假时长:${item.LVLV_HOUR}小时(共${item.toDay}天)</span>
                        </div>
                       <div class="weui-form-preview__item">
                           <label class="weui-form-preview__label black">休假时间</label>
                           <span class="weui-form-preview__value black f12">${item.LVLV_FRM_DTM}至${item.LVLV_TO_DTM}</span>
                       </div>
                       <div class="weui-form-preview__item">
                           <label class="weui-form-preview__label black">${item.SYSV_LVLV_TYPE_DESC}</label>
                           <span class="weui-form-preview__value black f12">申请时间:${item.ApplyDate}</span>
                       </div>
                     </div>
                     <div class="weui-form-preview__ft">${audlist.events.reportBtn(item)}</div>
                     </div>
                     `
                 }else if(ky=="OTBL"){
                     html+=`
                     <div class="weui-form-preview item" index="${item.OTBL_KY}" lvlvId="${item.OTBL_ID}" type="${item.OTBL_STS_DESC}">
                      <div class="weui-form-preview__hd">
                        <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black f12">${item.OTBL_ID}</label>
                          <em class="weui-form-preview__value">${item.OTBL_STS_DESC}</em>
                        </div>
                      </div>
                    <div class="weui-form-preview__bd f12">
                      <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black">${item.MEME_NAME}</label>
                          <span class="weui-form-preview__value black">加班时长:${item.OTBL_HOUR}小时</span>
                      </div>
                      <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black">加班时间</label>
                          <span class="weui-form-preview__value black f12">${item.OTBL_START_DT}至${item.OTBL_END_DT}</span>
                      </div>
                      <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black">申请时间</label>
                          <span class="weui-form-preview__value black f12">${item.ApplyDate}</span>
                      </div>
                     </div>
                     <div class="weui-form-preview__ft">${audlist.events.overTimeBtn(item)}</div>
                     </div>
                     `
                 }
            })
             return html;
            
        },
        businessOrReport(data){
              let html='';
              if(data.SYSV_ATDC_TYPE=="BSTP"){
                  html=` <label class="weui-form-preview__label black f12">发生日期:${data.ATDC_START_DT}起${data.ATDC_DAY}天<span class="ml15rem">${data.ATDC_HUMAN_CNT}人</span></label>`
              }else if(data.SYSV_ATDC_TYPE=="ETTN"){
                  html=`<label class="weui-form-preview__label black f12">发生日期:${data.ATDC_START_DT}<span class="ml15rem">${data.ATDC_HUMAN_CNT}人</span></label>`
              }
              return html
        },
        reportBtn(data){
            let html='';
            if(data.REVIEW_IND=="Y"&&data.AUDIT_BUTTON_FEATURE=="AUDIT"){
                html=`<a class="weui-form-preview__btn weui-form-preview__btn_primary through" href="javascript:">通过</a>`
            }else if(data.REVIEW_IND=="Y"&&data.AUDIT_BUTTON_FEATURE=="AGREE"){
                html=`<a class="weui-form-preview__btn weui-form-preview__btn_primary through" href="javascript:">已知晓</a>`
            }
            return html
        },
        reimburseBtn(data){
            let html='';
            if(data.REVIEW_IND == "Y"){
                html=`<button type="submit" class="weui-form-preview__btn through" href="javascript:">通过</button>`
            }
            return html
        },
        overTimeBtn(data){
            let html='';
            if(data.REVIEW_IND=="Y"&&data.AUDIT_FEATURE=="AUDIT"){
                html=`<a class="weui-form-preview__btn weui-form-preview__btn_primary through" href="javascript:">通过</a>`
            }else if(data.REVIEW_IND=="Y"&&data.AUDIT_FEATURE=="AGREE"){
                html=`<a class="weui-form-preview__btn weui-form-preview__btn_primary through" href="javascript:">已知晓</a>`
            }
            return html;
        },
        through(ev){
            ev.stopPropagation();
            ev.preventDefault();
            var ky=$(".buttons .select").attr("data-value");
            var $item=$(this).closest(".weui-form-preview");
            if(ky=="ATDC"){
                let atdcKy=$item.attr("index");
                let name=$item.attr("name");
                let startTime=$item.attr("start");
                let typeName=$item.find("div.weui-form-preview__hd label span").text();
                commumal.confirm({
                    title:"确定通过"+name+startTime+"的"+typeName+"申请?",
                    click:()=>{
                        audlist.events.reportThrough(atdcKy)
                    }
                })
            }else if(ky=="RBRB"){
                let text=$item.find('.weui-form-preview__hd .weui-form-preview__label').text()+"的"+$item.find('.cost .RBRB_APPLY_AMT').text()+"元的费用申请？";
                let rbrb_ky=$item.attr('rbrb_key').split(',');
                commumal.confirm({
                    title:"确定通过"+text,
                    click:()=>{
                        audlist.events.reimburseThrough(rbrb_ky)
                    }
                })
            }else if(ky=="LVLV"){
                 let lvlvKy=$item.attr("index");
                 let lvlvId=$item.attr("lvlvId");
                 let type=$item.attr("type");
                 commumal.confirm({
                     title:'确定通过单号为'+lvlvId+"的"+type+"申请？",
                     click:()=>{
                         audlist.events.askForLeaveThrough(lvlvKy)
                     }
                 })
            }else if(ky=="OTBL"){
                let otblKy=$item.attr("index");
                let otblId=$item.attr("lvlvid");
                commumal.confirm({
                    title:'确定通过单号为'+otblId+"的加班申请？",
                    click:()=>{
                        audlist.events.overTimeThrough(otblKy)
                    }
                })
            }
            
        },
        reportThrough(key){
            $.defs.loadingshow();
            $.defs.myPost({
                m:"UPDATE",
                SP_NAME:"SP_ATDC_ATTENDANCE_INFO_STATUS_UPDATE",
                REVIEW_IND:"Y",
                ATDC_KY:key
            }).then((data)=>{
                $.defs.loadinghide();
                if(data.RETURN_CODE==0){
                    weui.toast('操作成功', 3000);
                    audlist.events.fresh()
                }else{
                    $.defs.msg(data.RETURN_MESSAGE)
                }
            })
        },
        reimburseThrough(key){
            $.defs.loadingshow();
            let submitdata = [];
            $(key).each((i,v)=>{
                submitdata.push({
                    SP_NAME: 'SP_RBRB_REIMBURSE_INFO_STATUS_UPDATE',
                    m: 'UPDATE',
                    RBRB_KY: v,
                    REVIEW_IND: 'Y'
                })
            });
            $.defs.myPost({
                m: 'batch_update_transation',
                TB_NAME: 'asfdf',
                SP_NAME:"dasds",
                DETAIL_STRING: JSON.stringify(submitdata)
            }).then((data)=>{
                $.defs.loadinghide();
                if(data.RETURN_CODE==0){
                    weui.toast('操作成功', 3000);
                    audlist.events.fresh()
                }else{
                    $.defs.msg(data.RETURN_MESSAGE)
                }
            })

        },
        askForLeaveThrough(key){
            $.defs.loadingshow();
            $.defs.myPost({
                m:"UPDATE",
                SP_NAME:"SP_LVLV_LEAVE_INFO_STATUS_UPDATE",
                REVIEW_IND:"Y",
                LVLV_KY:key
            }).then((data)=>{
                $.defs.loadinghide()
                if(data.RETURN_CODE==0){
                    weui.toast('操作成功', 3000);
                    audlist.events.fresh()
                }else{
                    $.defs.msg(data.RETURN_MESSAGE)
                }
            })
        },
        overTimeThrough(key){
            $.defs.loadingshow();
            $.defs.myPost({
                m:"UPDATE",
                SP_NAME:"SP_OTBL_OVERTIME_BILL_INFO_STATUS_UPDATE",
                REVIEW_IND:"Y",
                OTBL_KY:key
            }).then((data)=>{
                $.defs.loadinghide()
                if(data.RETURN_CODE==0){
                    weui.toast('操作成功', 3000);
                    audlist.events.fresh()
                }else{
                    $.defs.msg(data.RETURN_MESSAGE)
                }
            })
        },
        matterItem(){
            var ky=$(".buttons .select").attr("data-value");
            var $item=$(this).closest(".weui-form-preview");
            localStorage.setItem("audited",ky)//记住离开时的位置
            if(ky=="ATDC"){
                let atdcKy=$item.attr("index");
                let atdcSts=$item.attr("sts");
               spspa.setMessage("auditing",{
                   content:{
                    atcdKy:atdcKy,
                    atdcSts:atdcSts
                   }
               })
               spspa.go("/records")
            }else if(ky=="RBRB"){
                spspa.setMessage('auditing',{
                    content:{
                        DATE:$item.attr("date"),
                        RBRB_STS:$item.attr("rbrb_sts"),
                        APPLY_EHUSER:$item.attr("ehuser")
                    }
                })
                spspa.go("/apply")
            }else if(ky=="LVLV"){
                let lvlvky=$item.attr("index");
                spspa.setMessage("auditing",{
                    content:{
                        lvlvKy:lvlvky,
                    }
                })
                spspa.go("/leave")
            }else if(ky=="OTBL"){
                let otblKy=$item.attr("index");
                spspa.setMessage('auditing',{
                    content:{
                        otblKy:otblKy
                    }
                })
                spspa.go("/work")
            }

        },
        queryMore(){
            spspa.go("/MoreList")
        }
        
    },
    config:{
        init(){
            let arr=JSON.parse(commumal.caching("BASE_INFO"));
            $(".top .huan").text("待审核")
            audlist.events.condition(arr.oa_type);
            let audited=commumal.caching("audited");//记住缓存
             if(audited){
                $(".buttons button[data-value="+audited+"]").addClass("select").siblings().removeClass("select")
              }
            audlist.events.fresh()
            
            
        }
    }
}
$(function(){
     audlist.config.init();
     audlist.methods.init();
})


